export const query = graphql`{
  home: allFile(
    sort: {fields: [relativePath]}
    filter: {relativePath: {regex: "/home(.*)/"}
    }) {
    nodes {
      childImageSharp {
        gatsbyImageData(
          layout: FULL_WIDTH
        )
      }
    }
  }
}`;
